/* Basic styling for the Schedule component */
.schedule-container {
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .schedule-container h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 30px;
    color: #333;
  }
  
  .schedule-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .schedule-item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background-color: #f1f1f1;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .schedule-item:hover {
    background-color: #e2e2e2;
  }
  
  .schedule-time {
    font-weight: bold;
    color: #333;
  }
  
  .schedule-event {
    color: #555;
  }
  