nav a:nth-child(7) {
  display: none;
}

  #m {
    display: none;
  }
  
  
  .checkbtn {
    font-size: 30px;
    color: white;
    background: none;
    border: none;
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 1000;
    cursor: pointer;
    display: none;

  }
  
  #check {
    display: none;
  }
  nav {

    background-color: black;
    color: white;
    padding: 0px 20px 1px 1%;

  }
  nav ul {
    font-size: large;
    list-style: none;
    background-color: transparent;
    margin-top: 0;
    margin-left: 50%;
    box-sizing: border-box;
    font-weight: bolder;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: flex; 
  }
  
  nav li {
    padding: 3%;
    display: inline-block;
  }
  
  nav a {
    position: relative;
    text-decoration: none;
    display: inline-block;
    padding: 0.5em 1em;
    color: aliceblue;
    z-index: 1;
    background-color: black;
    border-radius: 10%;
    box-shadow: 10px 0px 20px rgb(241, 5, 5);
  }
  
  nav a:hover {
    box-shadow: none;
  }
  
  
  nav span {
    padding: 4.5% 6% 0 0;
    position: absolute;
    background-image: conic-gradient(rgb(5, 5, 5), rgb(248, 10, 10), rgb(5, 5, 5), rgb(253, 47, 0));
    border-radius: 5px;
    top: 0%;
    left: 55.7%;
    transition: .5s;
    z-index: 0;
  }
  
  nav li:nth-child(2):hover~span {
    left: 55.7%;
  }
  
  nav li:nth-child(3):hover~span {
    margin-left: 7.3%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(4):hover~span {
    margin-left: 17%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(5):hover~span {
    margin-left: 26.5%;
    padding-left: 2%;
    opacity: 1;
  }
  
  nav li:nth-child(6):hover~span {
    margin-left: 35.7%;
    padding-left: 2%;
    opacity: 1;
  }
  

  @media screen and (max-width: 1024px) {
    #m {
      display: block;
      background-color: rgb(125, 124, 124);
      border-radius: 0%;
     
      color: white;
      font-weight: bold;
      padding:10% 10%;
      margin-left: -85%;
      margin-top: 0%;
      width: 100vw;
      border-top: 10px solid black;
      
    }
    #check:checked ~ ul {
      margin-left: -1%;
      /* margin-top: 50%; */
      padding: 0% 30%;
      background: linear-gradient(220deg, rgb(255, 255, 255), rgb(255, 255, 255));
      position: fixed;
      background-size: 30px;
      width: 100%;
      /* border-top:5px solid black; */
      
      display: block;
    }
 
    #check:checked ~ .checkbtn .fa-bars {
      display: none;
    }
    .checkbtn .fa-times {
      display: none; 
    }
    
    .checkbtn .fa-bars {
      display: block; 
    }
#check:checked ~ .checkbtn .fa-times {
  display: block;
  
}

    .checkbtn {
      display: block;
      position: fixed;
      margin-left: 90%;
      background: linear-gradient(45deg,black, rgb(254, 254, 254), rgb(5, 245, 137),rgb(0,0,0),black);
  
      -webkit-background-clip: text; 
      color: transparent;
      
      padding: 3%;
    }
    #check:not(:checked) ~ nav ul {
      display: none;
    }
    nav ul {
      
      
      margin-left: 30%;
      margin-top: 50%;
      padding: 0% 8% 8%;
      
      position: fixed;
      background-size: 30px 30px;
      display: none; 
    }
  
    nav li {
    
      display: block;
      
     
    }
  
    nav a {
      text-decoration: none;
      
      color: rgb(10, 162, 124);
      background: none;
      border-radius: none;
      box-shadow: none;
      /* display: block; */
      margin-top: 0%;
      margin-left: -80%;
     
      
      /* padding: one; */
      transition: all .5s;
    }
    a {
      margin-top: -1%;
    }
      nav a:hover {
        box-shadow: 10px 0px 20px rgb(9, 243, 231) ;
      }
      
    
  
    nav span {
      display: none;
    }

  
    #h{
      display: none;
    }
    #Ari {
      display: block;
      /* background-color: red; */
      border-radius: 0%;
      position: absolute;
      color: white;
      font-weight: bold;
      padding: 10%;
      margin-left: -37%;
      /* opacity: 0.4; */
      width: 100vw;
      height: 100vh;
      text-align: center;
     
  background: linear-gradient(220deg, rgba(32, 21, 64, 0.3), rgba(25, 54, 61, 0.817));
    }
  }
  